.contentContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.portfolioTitle {
    width: 60%;
    font-size: 2rem;
    margin: 2rem 0 0 0;
    color: #a3acb2;

}

.projectsContainer {
    width: 60%;
    display: flex;
    margin: 0 auto;
}

@media screen and (max-width: 1500px) {
    .portfolioTitle {
        width: 80%;
        font-size: 2rem;
        margin: 2rem 0 0 0;
        color: #a3acb2;
    
    }

    hr {
        width: 80%;
    }
    
    .projectsContainer {
        width: 80%;
        display: flex;
        margin: 0 auto;
    }
}
    